import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PoDirectComponent } from './po-direct.component';

const routes: Routes = [
    {
        path: '', component: PoDirectComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PoDirectRoutingModule { }



