import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { PoDirectRoutingModule } from './po-direct-routing.module';
import { PoDirectComponent } from './po-direct.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PoDirectRoutingModule
  ],
  declarations: [
    PoDirectComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class PoDirectModule { }



